import _ from "lodash";
import { AppConfig } from "tonva";
import { tvs } from "./tvs";
import { jnkTop, assistjnkTop, GLOABLE } from "./ui";
import { Sales } from "./model/sales";

const appConfigBase: AppConfig = {
    appName: "/salestask",
    version: "1.1.93", // uqs
    tvs: tvs,
    loginTop: undefined,
    oem: ""
};

export const appConfig: AppConfig = _.merge(_.clone(appConfigBase), {
    loginTop: jnkTop
});

export const assistAppConfig: AppConfig = _.merge(_.clone(appConfigBase), {
    loginTop: assistjnkTop
});

export const setting = {
    pageHeaderCss: "bg-primary",
    sales: undefined as Sales,
    couponType: { "coupon": "", "credits": "", "vipcard": "VIP" }
};

const agentConfig = {
    documentTitle: "",
    favicon: "/logo.png",
    navSettings: appConfig,
}

const assistConfig = {
    documentTitle: "",
    favicon: "/assistlogo.png",
    navSettings: assistAppConfig,
}

export const currentAppConfig = GLOABLE.IsAssistApp ? assistConfig : agentConfig